/* src/App.css - mobile improvements (title, tabs, filter, spacing, table, chart x-labels) */

:root{
  --bg:#f6fbfc;
  --card:#ffffff;
  --muted:#9aa6b2;
  --accent-start:#2bd0c8;
  --accent-end:#1f6ef8;
  --pill:#e9f6ff;
  --expectation:#d94b4b;
}

*{box-sizing:border-box;font-family: 'Helvetica Neue', Arial, sans-serif}
body,html,#root{height:100%;margin:0;background:var(--bg);color:#0b1a23}

.app{max-width:920px;margin:0 auto;padding:18px 12px 80px}
.app-header{display:flex;align-items:flex-start;justify-content:space-between;gap:12px;flex-wrap:wrap}
.title{font-size:44px;margin:0 0 6px 0;font-weight:800;line-height:1;letter-spacing:-1px}
.tab-buttons{display:flex;flex-direction:row;gap:8px;align-items:center} /* 横並びに */
.tab{border-radius:20px;padding:8px 14px;border:none;background:transparent;cursor:pointer;white-space:nowrap}
.tab.active{background:linear-gradient(90deg,var(--accent-start),var(--accent-end));color:white;box-shadow:0 4px 14px rgba(31,110,248,0.18)}

.main{margin-top:10px}
.top-row{display:flex;justify-content:space-between;align-items:flex-start;gap:12px;flex-wrap:wrap}
.avg-card{background:var(--card);padding:14px;border-radius:14px;box-shadow:0 8px 20px rgba(15,33,62,0.06);min-width:180px}
.avg-label{color:var(--muted);font-size:13px}
.avg-number{font-size:30px;font-weight:800;margin-top:6px}

.filter-row{display:flex;flex-direction:row;align-items:center;gap:10px;flex-wrap:wrap;min-width:200px}
.filter-label{color:var(--muted);font-size:14px}
.filter-select{padding:8px;border-radius:8px;border:1px solid #e6eef5;background:#fff;min-width:120px}
.reset-ui-btn{padding:8px 10px;border-radius:12px;border:none;background:#fff;color:#1b6ea8;cursor:pointer;box-shadow:0 6px 12px rgba(13,26,40,0.04)}

.controls{display:flex;flex-direction:column;gap:8px;margin-top:12px}
.control-item{background:transparent;padding:6px 0}
.control-head{display:flex;align-items:center;gap:8px}
.info-btn{border-radius:50%;width:26px;height:26px;background:#eef6fb;border:none;cursor:pointer;display:inline-flex;align-items:center;justify-content:center}
.control-item input[type="range"]{width:100%}
.legend{font-size:12px;color:var(--muted);margin-top:6px}
.tooltip{background:#fff;padding:8px 12px;border-radius:10px;box-shadow:0 8px 20px rgba(12,28,58,0.06);margin-top:8px;font-size:13px}

.date-row{margin-top:8px}
.date-row input[type="date"]{padding:8px;border-radius:10px;border:1px solid #e6eef5;background:#fff}

.input-section{margin-top:12px}
.recommendation-row{display:flex;align-items:center;gap:12px;flex-wrap:wrap}
.rec-label{font-weight:700;margin-right:6px}
.rec-pill{display:flex;align-items:center;gap:10px;background:#fff;padding:8px 12px;border-radius:18px;box-shadow:0 6px 14px rgba(15,33,62,0.06)}
.rec-emoji{font-size:20px}
.rec-text{font-weight:700}
.rec-score{margin-left:8px;color:var(--muted)}

.hand-cards{display:flex;gap:10px;margin-top:12px;overflow-x:auto;padding-bottom:4px}
.hand-cards::-webkit-scrollbar{height:8px}
.hand-card{min-width:140px;flex:0 0 140px;background:linear-gradient(180deg,#ffffff,#fbfdff);padding:14px;border-radius:12px;box-shadow:0 10px 30px rgba(12,28,58,0.06);text-align:center;cursor:pointer;border:2px solid transparent}
.hand-card.selected{border-color:#2ea8ff}
.hand-emoji{font-size:32px}
.hand-text{margin-top:8px;font-weight:700}
.hand-sub{margin-top:8px;color:var(--muted);font-size:12px;display:flex;flex-direction:column;gap:4px;text-align:center}
.plays-label,.avg-label{display:block}

/* ensure result pill row uses less vertical space on mobile */
.result-row{display:flex;gap:10px;margin-top:12px;flex-wrap:wrap}
.pill{padding:8px 12px;border-radius:18px;border:none;background:var(--pill);cursor:pointer;box-shadow:0 6px 20px rgba(17,34,63,0.04);font-size:14px}
.pill.active{background:linear-gradient(90deg,var(--accent-start),var(--accent-end));color:white}

.actions-row{margin-top:12px}
.save-btn{padding:12px 18px;border-radius:16px;border:none;background:linear-gradient(90deg,var(--accent-start),var(--accent-end));color:white;font-weight:700;box-shadow:0 8px 22px rgba(31,110,248,0.18);cursor:pointer;font-size:15px}

.back-row{margin-top:8px}
.back-btn{padding:6px 10px;border-radius:10px;background:#f3f7fa;border:none;cursor:pointer;box-shadow:0 6px 12px rgba(13,26,40,0.04);font-size:13px}

.history-section{margin-top:18px}
.history-table{width:100%;border-collapse:collapse;background:var(--card);border-radius:10px;overflow:hidden;box-shadow:0 10px 30px rgba(12,28,58,0.06)}
.history-table thead tr{background:transparent}
.history-table th{text-align:left;padding:12px 12px;color:#0b2230;font-weight:700;font-size:13px;border-bottom:1px solid #eef5fb}
.history-table td{padding:12px 12px;border-bottom:1px solid #f3f7fb;font-size:14px}
.td-hand{text-align:center;font-size:18px}
.small-btn{padding:6px 8px;border-radius:10px;border:none;background:#eef6fb;color:#1b6ea8;cursor:pointer;font-size:13px;min-width:56px}

/* make "あいこ" not wrap into two lines */
.history-table td { word-break: keep-all; white-space: nowrap; }

/* stats */
.stats-section{margin-top:18px}
/* chart-wrap: horizontal scroll only; height fixed so vertical fits the screen */
.chart-wrap{position:relative;height:240px;background:transparent;overflow-x:auto;overflow-y:hidden;padding-bottom:18px}
.chart-inner{min-width:600px;height:240px;padding-bottom:36px} /* bottom padding to avoid cutting x-labels */

.hand-summary-cards{display:flex;gap:10px;margin-top:14px;overflow-x:auto}
.summary-card{min-width:160px;background:var(--card);padding:10px;border-radius:12px;box-shadow:0 8px 20px rgba(12,28,58,0.06);flex:0 0 160px;text-align:left}
.summary-emoji{font-size:22px}
.summary-title{font-weight:700;font-size:16px;margin-top:6px}
.summary-sub{color:var(--muted);font-size:12px}
.summary-score{font-weight:800;font-size:18px;margin-top:8px;text-align:right}

/* small-screen tweaks */
@media (max-width:520px) {
  .title{font-size:36px}
  .avg-number{font-size:28px}
  .app{padding:14px 10px 80px}
  .tab-buttons{gap:6px}
  .filter-select{min-width:100px}
  .hand-card{min-width:130px;flex:0 0 130px;padding:12px}
  .chart-inner{min-width:520px}
  .chart-wrap{height:220px}
  .history-table th, .history-table td{padding:10px 8px;font-size:13px}
  .small-btn{padding:6px 8px;font-size:12px;min-width:48px}
}

/* very small screens */
@media (max-width:380px) {
  .title{font-size:32px}
  .hand-card{min-width:120px}
  .summary-card{min-width:140px}
  .chart-inner{min-width:480px}
}
